import { Container } from '@material-ui/core';
import React from 'react';
import VerticalNav from "../components/VerticalNav"

const textStyle = {
    color: "#F3ECE3"
}

class Gym extends React.Component {
    constructor(props) {
        super(props);

    }
    render() {
        return (
            <Container>
                <div style={textStyle}>
                    Currently I am doing Push Pull Legs 6 days a week
                </div>
            </Container>



        );

    }
}
export default Gym;