import React from 'react';
import { Container } from '@material-ui/core';

const textStyle= {
    color: "#F3ECE3"
  }
class Work extends React.Component {
    constructor(props) {
        super(props);

    }
    render() {
        return (
            <div style= {textStyle}>
                <Container>
                    I work for Liberty Mutual Insurance Group as a SE.
                </Container>

            </div>

        );

    }
}
export default Work;