import React from 'react';
import Nav from "react-bootstrap/Nav"
import Tab from 'react-bootstrap/Tab'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

class VerticalNav extends React.Component {
    constructor(props) {
        super(props);

    }
    render() {
        return (
            <Tab.Container id="left-tabs" defaultActiveKey="push">
                <Row noGutters="true"> 
                    <Col sm={3}>
                        <Nav variant="tabs" className="flex-column">
                            <Nav.Item>
                                <Nav.Link eventKey="push">Push</Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link eventKey="pull">Pull</Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link eventKey="legs">Legs</Nav.Link>
                            </Nav.Item>
                        </Nav>
                    </Col>
                    <Col sm={9}>
                        <Tab.Content>
                            <Tab.Pane eventKey="push">
                                a
                            </Tab.Pane>
                            <Tab.Pane eventKey="pull">
                                b
                            </Tab.Pane>
                            <Tab.Pane eventKey="legs">
                                c
                            </Tab.Pane>
                        </Tab.Content>
                    </Col>
                </Row>
            </Tab.Container>
        )
    }
}
export default VerticalNav;