import React from 'react';
import { Container } from '@material-ui/core';
import Button from 'react-bootstrap/Button'


const textStyle= {
  color: "#F3ECE3"
}
class Books extends React.Component {
  constructor(props) {
    super(props);

  }


  render() {
    return (
      <div style={textStyle}>
        <Container >
          <h1>
            Books
          </h1>
          <li>
            Clean Code
          </li>
        </Container>

      </div>

    );

  }
}
export default Books;